* {
  margin: 0;
  padding: 0;
}

html {
  font-family: Roboto, sans-serif;
}

body {
  --second-color: #00acdc;
}

h1 {
  display: none;
}

.container {
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
}

.header_logo {
  height: 40px;
}

.nav_list {
  gap: 20px;
  list-style: none;
  display: flex;
}

.nav_link {
  color: #000;
  text-transform: uppercase;
  justify-content: center;
  align-items: center;
  height: 60px;
  font-size: 12px;
  font-style: normal;
  font-weight: 500;
  line-height: normal;
  text-decoration: none;
  display: flex;
  position: relative;
}

.nav_link:hover, .is-active {
  color: var(--second-color);
}

.is-active:after {
  content: "";
  background-color: var(--second-color);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  display: block;
  position: absolute;
  bottom: 0;
}

